import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AddSeriePage } from "../add-serie/add-serie";
import { SearchApi } from "./../../models/searchapi.model";
import { ApiServiceProvider } from "./../../providers/api-service/api-service";

@IonicPage()
@Component({
  selector: "page-search-serie",
  templateUrl: "search-serie.html"
})
export class SearchSeriePage {
  apiSearchSeries: SearchApi = new SearchApi();
  apiSearchSerie: SearchApi;
  searchInput: string;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiServiceProvider: ApiServiceProvider
  ) {}
  onInput(searchInput) {
    this.apiServiceProvider
      .getSerieApi(searchInput)
      .then(apiSearchSeriesFetched => {
        this.apiSearchSeries = apiSearchSeriesFetched;
      });
  }
  addSerieApi(imdbID) {
    this.apiServiceProvider.addSerieApi(imdbID).then(num => {
      this.apiSearchSerie = num;
      this.navCtrl.push(AddSeriePage, { apiSearchSerie: this.apiSearchSerie });
    });
  }
}



