import { Injectable } from '@angular/core';
import { Http } from "@angular/http";
import { SearchApi } from "./../../models/searchapi.model";
import { SerieSearchApi } from "./../../models/seriesearchapi.model";
import "rxjs/add/operator/toPromise";
import "rxjs/add/operator/map";

@Injectable()
export class ApiServiceProvider {
  private baseUrl: string = "http://www.omdbapi.com/";
  private apiKey: string = "a693a781";
  titleSearch: string;
  imdbIDSearch: any;

  constructor(public http: Http) {}
  public getSerieApi(searchInput): Promise<any> {
    this.titleSearch = searchInput;
    const url = `${this.baseUrl}?apiKey=${this.apiKey}&type=series&s=${this.titleSearch}&page=1-100`;
    return this.http
      .get(url)
      .toPromise()
      .then(response => response.json() as SearchApi)
      .catch(error => console.log("Une erreur est survenue " + error));
  }
  public addSerieApi(imdbID): Promise<any> {
    this.imdbIDSearch = imdbID;
    const url = `${this.baseUrl}?apiKey=${this.apiKey}&type=series&i=${this.imdbIDSearch}`;
    return this.http
      .get(url)
      .toPromise()
      .then(response => response.json() as SerieSearchApi)
      .catch(error => console.log("Une erreur est survenue " + error));
  }
}



